.section.coming-soon-get-updates {
    background: url('/wp-content/themes/bokka-wp-theme-child/assets/build/images/floorplan-background-faded.jpg') no-repeat;
    background-size: cover;
    background-position: center center;
}
.section.coming-soon-get-updates .container {
    -webkit-box-align: center;
    -webkit-align-items: center;
        -ms-flex-align: center;
                -ms-grid-row-align: center;
            align-items: center;
    text-align: center;
    padding: 60px 0;
}
@media (min-width: $(tablet)) {
    .section.coming-soon-get-updates .container {
        padding: 120px;
    }
}
.section.coming-soon-get-updates .title {
    margin-bottom: 30px;
}
.section.coming-soon-get-updates .text {
    margin: 0 auto;
}
@media (min-width: $(desktop)) {
    .section.coming-soon-get-updates .text {
        width: 50%;
    }
}
